@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap');

* {
  margin:0;
  padding: 0;
  font-family: 'Lato';
  box-sizing: border-box;
}

.container {
  display: flex;
}

a:visited {
  text-decoration: none;
}

.rLink,
.rLink:link,
.artistLink:link,
.artistLink,
.songLink {
  text-decoration: none;
  color: black;
  
}

.trackCtn * {
  background-color: #ffffff;
}

.videoplayer {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.videoplayerCloseBtn {
  cursor: pointer;
}

.sim-artist-ctn,
.sim-songs-ctn {
  flex:4;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0px 0px 1px .5px rgba(0,0,0,.3);
}

