@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap);
.trackContent {
  padding: 10px 5px; 
  margin-bottom: 10px;
  border-bottom: 1px solid #f7f7f7;
}

.trackContent * {
  display: inline-block;
  padding: 0 5px;
}

.trackName p {
  display: block;
  text-align: left;
}

#playButton,
#saveButton {
  height: 28px;
  width: 28px;
}

#playButton:hover {
  cursor: pointer;
}
.mainContentPage {
  text-align: center;
  flex:4 1;
  padding: 15px;
}

#searchInput {
  border: 1px solid #bcbec2;
  height: 40px;
  max-width: 484px;
  width: auto;
  padding: 0 10px;
  border-radius: 14px;
  font-size: 16px;
  outline: none;
}

.trackCtn {
  max-width: 425px;
  margin: 10px auto;
  padding: 10px;
  text-align: left;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 0px 1px .5px rgba(0,0,0,.3);
  padding-bottom: 300px;
}

@media only screen and (max-width: 540px) {
  /* For mobile phones: */
 
  .searchCtn {
    
    height: calc(100vh - 153px);
  }


}

.moremenu {
  display: none;
  position: absolute;
  
  min-width: 80px;
  z-index: 2;
  border: .4px solid rgb(230, 227, 227);
  bottom: -10px;
  box-shadow: 5px 8px 16px 5px rgba(0,0,0,0.2);
  left: -5px;
  top: 14px;
  
}

.moremenu ul {
  list-style-type: none;
  width: 80px;
  font-size: 10px;
  text-align: left;
  
}

.moremenu li {
  text-align: left;
  width: 80px;
  padding: 10px 5px;
  background-color: #ffffff;
}

.moremenu li:hover {
  background-color: rgb(211, 199, 182);
}

.dropdown:hover .moremenu {
  display: block;
}


.remove-btn:hover {
  cursor: pointer;
}
.playlist-item-ctn {
  display: flex;
  list-style-type: none;
  align-items: center;
  border-bottom: .5px solid #fff;
}

.playlist-item-ctn * {
  
  text-align: center;
  
}

.playlist-item-ctn img {
  width: 28px;
  height: auto;
}

.playlist-item-ctn img:hover {
  cursor: pointer;
}

.play-ctn,
#more-icon {
  width: 15%;
}

#more-icon img {
  width: 22px;
  height: auto;
}

.track-name,
.track-artist {
  width: 35%;
}

.dropdown {
  position: relative;
  display: inline-block;
}


.likedsongs {
  flex: 4 1;
  padding: 10px 5px;
  padding-bottom: 390px;
}

.likedsongs ul {
  list-style-type: none;

}

/* @media only screen and (min-width: 600px) {
  .likedsongs {
    margin: 0 auto;
    width: 600px;
  }
} */

