    /* padding-top: 65px; */
  }
}
.home {
  flex: 4 1;
  padding: 10px 20px;
}

.homeContainer {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media only screen and (max-width: 540px) {
  /* For mobile phones: */


  .homeWrapper {
    /* display: flex;
    flex-direction: column; */
    /* min-height: calc(100vh - 200px); */
    /* flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: stretch;
    align-content: stretch; */
    /* height: calc(100vh - 153px); */
    height: calc(100vh - 65px);
    
  }


}
.playlistCardImg {
  font-family: 'Lato'!important;
}
.topbar {
  width: 100%;
  height: 65px;
  background-color: rgb(226, 224, 224);
  box-shadow: 0px 2px 10px rgb(0 0 0 / 15%);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 5;
}

.topbarWrapper {
  height: 100%;
  padding: 0 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 29px;
  font-weight: 800;
  cursor: pointer;
}

.topRight {
  display: flex;
  align-items: center;
}

.topbarIcon {
  margin: 0 5px;
  cursor: pointer; 
  color: #555;
}

.topAvatar {
  margin-left: 10px;
}
.sidebar {
  color: green;
  flex: 1 1;
  position: -webkit-sticky;
  position: sticky;
  top: 65px;
  height: calc(100vh - 65px);
  background-color: rgba(187, 186, 186, 0.2);
  box-shadow: 0px 2px 10px rgb(0 0 0 / 15%);
}

.sidebarWrapper {
  padding: 20px;
}

.sidebarTitle {
  font-size: 14px;
  color: rgb(148, 147, 147);
  padding: 10px 0;
}

.sidebarList {
  list-style-type: none;
  padding: 10px;
  
}

.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  color: rgb(46, 45, 45);
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  border-radius: 10px;
  
}

.sidebarListItem.active,
.sidebarListItem:hover {
  background-color: rgba(158, 157, 157, 0.2);

}

.sidebarIcon {
  margin-right: 6px;
}

@media only screen and (max-width: 540px) {
  /* For mobile phones: */
  .sidebar{
    position: absolute;
    overflow: hidden;
    bottom: 0;
    background-color: rgb(209, 208, 208);
    z-index: 10;
    height: 75px;
    /* flex: 0 0 75px; */
  }

  .sidebarWrapper {
    padding: 0;
  }

  .sidebarTitle {
    display: none;
  }

  .sidebarList {
    display: flex;
    justify-content: space-around;
  }

  .sidebarListItem {
    padding: 5px;
    cursor: pointer;
    color: rgb(46, 45, 45);
    display: flex;
    align-items: center;
    border-radius: 10px;
    flex-direction: column;
    flex: 1 1;
    
    
  }

  .sidebarLogout {
    display: none !important;
  }
}
* {
  margin:0;
  padding: 0;
  font-family: 'Lato';
  box-sizing: border-box;
}

.container {
  display: flex;
}

a:visited {
  text-decoration: none;
}

.rLink,
.rLink:link,
.artistLink:link,
.artistLink,
.songLink {
  text-decoration: none;
  color: black;
  
}

.trackCtn * {
  background-color: #ffffff;
}

.videoplayer {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.videoplayerCloseBtn {
  cursor: pointer;
}

.sim-artist-ctn,
.sim-songs-ctn {
  flex:4 1;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0px 0px 1px .5px rgba(0,0,0,.3);
}

@media only screen and (max-width: 540px) {
  /* For mobile phones: */
  .App{
