}

.loginBarWrapper {
  height: 100%;
  padding: 0 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 29px;
  font-weight: 800;
  cursor: pointer;
}

.topRight {
  display: flex;
  align-items: center;
}

.loginBarIcon {
  margin: 0 15px;
}

.loginBtn {
  color: white !important;
  font-size: 24px !important;
  text-transform: none !important;
  font-family: 'Lato'!important;
}

.demoBtn {
  color: white !important;
  font-size: 14px !important;
  font-family: 'Lato'!important;
}

